export default {
  layout: "Profile-content",
  mounted() {
    this.getTraining();

  },
  data() {
    return {
      trainings: '',
      training: {
          training_name: "",
          duration: "",
          training_year: "",
          institute_name: "",
      },
      errors: {},
    };
  },
  methods: {
    getTraining() {
      this.$axios
        .$get("/training/show")
        .then((res) => {
          console.log(res);
          this.trainings = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    trainingEdit(id) {
      $("#trainingUpdate").modal("show");
      this.$axios
        .$get("/training/edit/" + id)
        .then((res) => {
          this.training = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    trainingUpdate() {
      this.$axios
        .$post("/training/update/" + this.training.id, this.training)
        .then((res) => {
          this.getTraining();
          $("#trainingUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";


        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    trainingDelete(id) {
      if (confirm("Are you sure to delete this training?")) {
        this.$axios
          .$get("/training/delete/" + id)
          .then((res) => {
            this.getTraining();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
 





  },
};
